import React, { useRef, useState } from 'react'
import { VscArrowLeft, VscZoomIn, VscZoomOut } from 'react-icons/vsc'
import { useSelector } from 'react-redux'
import { A11y, Navigation, Pagination, Zoom } from 'swiper'
// Import Swiper styles
import 'swiper/css'
import 'swiper/css/lazy'
import 'swiper/css/navigation'
import 'swiper/css/pagination'
import 'swiper/css/zoom'
import { Swiper, SwiperSlide } from 'swiper/react'
import { selectBuildImage, selectBuildSlides } from '../../../store/build'
import AccessoryDetailsSidebar from '../AccessoryDetailsSidebar/AcccessoryDetailsSidebar'
import BuildAccessoriesList from '../BuildAccessoriesList/BuildAccessoriesList'
import {
  ImageWrapper,
  Sidebar,
  SidebarBack,
  SidebarInner,
  SidebarWrapper,
  SlidesFooter,
  SzSwiperWrapper,
  Wrapper,
  WrapperInner,
  ZoomButton,
} from './style'

export default function AccessoriesPicker() {
  const image = useSelector(selectBuildImage)
  const [showDetails, setShowDetails] = useState(false)
  const [showingAccessoryId, setshowingAccessoryId] = useState('1')
  const ref = useRef<HTMLDivElement>(null)
  // const dispatch = useDispatch()
  const slides = useSelector(selectBuildSlides)
  const showAccessory = (accessoryId: string) => {
    setShowDetails(true)
    setshowingAccessoryId(accessoryId)
  }

  return (
    <Wrapper>
      <WrapperInner>
        <ImageWrapper>
          {slides && slides.length > 0 ? (
            <SzSwiperWrapper>
              <Swiper
                /* @ts-ignore */
                ref={ref}
                modules={[Navigation, Pagination, A11y, Zoom]}
                navigation
                pagination={{ clickable: true }}
                spaceBetween={0}
                slidesPerView={1}
                zoom
                preloadImages={false}
                lazy
                onSlideChange={() => console.log('slide change')}
                onSwiper={(swiper) => console.log(swiper)}>
                {slides?.map((slide, idx) => (
                  <SwiperSlide zoom key={idx}>
                    <img src={slide.url} alt="" />
                  </SwiperSlide>
                ))}
              </Swiper>
              <SlidesFooter>
                <ZoomButton
                  onClick={() => {
                    /* @ts-ignore */
                    ref.current.swiper.zoom.out()
                  }}>
                  <VscZoomOut />
                </ZoomButton>
                <ZoomButton
                  onClick={() => {
                    /* @ts-ignore */
                    ref.current.swiper.zoom.in()
                  }}>
                  <VscZoomIn />
                </ZoomButton>
              </SlidesFooter>
            </SzSwiperWrapper>
          ) : (
            <img src={'' + process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
          )}
        </ImageWrapper>
        <SidebarWrapper>
          <SidebarInner>
            <Sidebar isActive={!showDetails}>
              <BuildAccessoriesList onDetailsClick={(id: string) => showAccessory(id)} />
            </Sidebar>
            <Sidebar isActive={showDetails}>
              <SidebarBack style={{ cursor: 'pointer' }} onClick={() => setShowDetails(false)}>
                <VscArrowLeft /> Back
              </SidebarBack>
              <AccessoryDetailsSidebar id={showingAccessoryId}></AccessoryDetailsSidebar>
            </Sidebar>
          </SidebarInner>
        </SidebarWrapper>
      </WrapperInner>
    </Wrapper>
  )
}
