import React, { useCallback, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { config, useTransition } from 'react-spring'
import { callWebhook, selectFullBuild } from '../store/build'
import {
  nextScreen,
  prevScreen,
  selectCurrentStep,
  selectCurrentUiScreen,
  selectNextButtonLabel,
  selectScreenOrder,
  UIScreenTitles,
} from '../store/ui'
import { LightenDarkenColor } from '../utils/utils'
import './App.css'
import BuildStatusBox from './components/BuildStatusBox'
import { DynamicScreen } from './components/DynamicScreen/DynamicScreen'
import FooterNav from './components/FooterNav'
import StepTitles from './components/StepTitles/StepTitles'
import { AnimatedScreen, AppHeader, AppWrapper, Container, ScreenSection } from './style'

declare global {
  interface Window {
    szReactPlugin: {
      webhook_url: string
      accent_color: string
      email_endpoint: string
      require_user_contact_details_upfront: boolean
    }
  }
}

if (window.szReactPlugin) {
  document.documentElement.style.setProperty('--accent-dark', window.szReactPlugin.accent_color)
  document.documentElement.style.setProperty(
    '--accent-dark-hover',
    LightenDarkenColor(window.szReactPlugin.accent_color, 15),
  )
  document.documentElement.style.setProperty(
    '--accent-dark-trans',
    LightenDarkenColor(window.szReactPlugin.accent_color, 80),
  )
  document.documentElement.style.setProperty(
    '--input-outline-hover',
    LightenDarkenColor(window.szReactPlugin.accent_color, 80),
  )
}

function App() {
  const currentScreen = useSelector(selectCurrentUiScreen)
  const currentScreenIdx = useSelector(selectCurrentStep)
  const screenNames = useSelector(selectScreenOrder)
  const fullBuild = useSelector(selectFullBuild)
  const nextLabel = useSelector(selectNextButtonLabel)
  const [canAdvance, setCanAdvance] = useState(true)
  const dispatch = useDispatch()
  const transition = useTransition(currentScreen, {
    from: { transform: 'translateX(10%)', opacity: 0 },
    enter: { opacity: 1, transform: 'translateX(0%)' },
    leave: { opacity: 0, transform: 'translateX(-0%)' },
    config: config.slow,
  })

  const handleValidChange = useCallback(
    (isValid: boolean, screen: string) => {
      if (screen === currentScreen) {
        setCanAdvance(isValid)
      }
    },
    [currentScreen],
  )

  const handleNextClick = () => {
    if (
      (currentScreen === UIScreenTitles.YOUR_DETAILS || currentScreen === UIScreenTitles.YOUR_ADDRESS) &&
      window.szReactPlugin?.webhook_url
    ) {
      dispatch(callWebhook(window.szReactPlugin?.webhook_url, fullBuild))
    }
    dispatch(nextScreen())
  }

  return (
    <AppWrapper className="szapp">
      <Container>
        <AppHeader>
          <StepTitles titles={screenNames} activeStep={screenNames[currentScreenIdx]} />
          <BuildStatusBox />
        </AppHeader>
        <ScreenSection>
          {transition((style, theCurrentScreen, t) => (
            <AnimatedScreen style={style} $isActive={theCurrentScreen === currentScreen}>
              <DynamicScreen screen={theCurrentScreen} onValidChange={handleValidChange} />
            </AnimatedScreen>
          ))}
        </ScreenSection>
        <FooterNav
          onNextClick={handleNextClick}
          onPrevClick={() => {
            dispatch(prevScreen())
            setCanAdvance(true)
          }}
          showNext={currentScreenIdx < screenNames.length - 1}
          showPrev={true}
          prevEnabled={currentScreen !== UIScreenTitles.SELECT_PRODUCT}
          nextEnabled={canAdvance}
          nextLabel={nextLabel}
          prevLabel="Back"
        />
      </Container>
    </AppWrapper>
  )
}

export default App
